      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.data-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}
.reliability {
