.result {
  background: rgb(248,248,248);
  -webkit-animation: spread-in 1s forwards;
          animation: spread-in 1s forwards;
  padding: 2em 3em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-x: visible;
  overflow-y: auto;
}
@-webkit-keyframes spread-in {
  from { height: 0; }
  to { height: calc(100vh - 155px); }
}
@keyframes spread-in {
  from { height: 0; }
  to { height: calc(100vh - 155px); }
}


